import api from "@/services/api";
import { InterestArea } from "@/types";
import {
  createResourceModule,
  ResourceState,
} from "./generic/createResourceModule";

export type InterestAreaState = ResourceState<InterestArea>;

export default createResourceModule<InterestArea>(async (interestAreaId) => {
  const res = await api.get(`/interest-area/${interestAreaId}/`);
  return res.data.result;
});
