import { RouteRecordRaw } from "vue-router";
import Index from "@/views/Index.vue";
import List from "@/views/interestArea/List.vue";
import Create from "@/views/interestArea/Create.vue";
import Details from "@/views/interestArea/Details.vue";
import Edit from "@/views/interestArea/Edit.vue";

const interestAreasConfig: RouteRecordRaw = {
  path: "/interesse",
  component: Index,
  children: [
    { path: "", component: List, name: "interest-areas" },
    { path: "criar", component: Create, name: "interest-area-create" },
    { path: ":id", component: Details, name: "interest-area-details" },
    { path: ":id/editar", component: Edit, name: "interest-area-edit" },
  ],
};

export default interestAreasConfig;
