
import ReservationsTable from "@/components/reservations/ReservationsTable.vue";
import ReservationsTopNav from "@/components/reservations/ReservationsTopNav.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: { ReservationsTable, ReservationsTopNav },
  created() {
    this.$store.dispatch("reservations/load", { offset: 1 });
  },
  computed: {
    reservations() {
      return this.$store.state.reservations.data;
    },
  },
});
