<template>
  <h1>Detalhes da Oferta</h1>
  <p>
    <strong>Instituição: </strong>
    <router-link
      :to="{ name: 'institution-details', params: { id: institution?.id } }"
      >{{ institutionName }}</router-link
    >
  </p>
  <p><strong>Curso: </strong> {{ courseName }}</p>
  <p><strong>Disciplina: </strong> {{ disciplineName }}</p>
  <p><strong>Inicia em: </strong> {{ $datetime(offer?.startDate) }}</p>
  <p><strong>Termina em: </strong> {{ $datetime(offer?.endDate) }}</p>
  <p><strong>Vagas disponíveis: </strong> {{ offer?.availableSeats }}</p>
  <p><strong>Vagas totais: </strong> {{ offer?.totalSeats }}</p>
  <p><strong>Status: </strong> {{ offer?.status }}</p>
  <p><strong>Desconto: </strong> {{ offer?.discountPercentage }}%</p>
</template>

<script lang="ts">
import { Institution, Offer } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  created() {
    this.$store.dispatch("offer/load", this.$route.params.id);
  },
  computed: {
    offer(): Offer | undefined {
      return this.$store.state.offer.data;
    },
    institution(): Institution | undefined {
      return this.offer?.educationInstitutionCourseDiscipline
        ?.educationInstitutionCourse?.educationInstitution;
    },
    institutionName(): string {
      return `${this.institution?.name} (${this.institution?.abbreviation})`;
    },
    courseName(): string {
      return (
        this.offer?.educationInstitutionCourseDiscipline
          ?.educationInstitutionCourse?.course.name || ""
      );
    },
    disciplineName(): string {
      return this.offer?.educationInstitutionCourseDiscipline?.name || "";
    },
  },
});
</script>
