<template>
  <back :to="{ name: 'interest-areas' }" />
  <h1>Cadastrando Área de Interesse</h1>
  <interest-area-form submitText="Cadastrar área de interesse" @send="submit" />
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import InterestAreaForm from "@/components/interestAreas/InterestAreaForm.vue";
import api from "@/services/api";
import { defineComponent } from "vue";

export default defineComponent({
  components: { InterestAreaForm, Back },
  methods: {
    async submit(data: unknown) {
      console.log("trying to submit", data);
      try {
        await api.post("interest-area", data);
        this.$store.dispatch("interestAreas/load", 1);
        this.$router.push({ name: "interest-areas" });
      } catch (e) {
        console.log(e);
      }
    },
  },
});
</script>
