import { Reservation } from "@/types";
import {
  ResourceState,
  createResourceModule,
} from "./generic/createResourceModule";
import api from "@/services/api";

export type ReservationState = ResourceState<Reservation>;

export default createResourceModule<ReservationState>(async (reservationId) => {
  const res = await api.get(`/student/reservations/${reservationId}`);
  console.log("reservation is", res.data);
  return res.data.result;
});
