import { createStore, Store } from "vuex";
import auth, { AuthState } from "./auth";
import institutions, { InstitutionsState } from "./institutions";
import blogPosts, { BlogState } from "./blogPosts";
import blogPost, { BlogPostState } from "./blogPost";
import admins, { AdminsState } from "./admins";
import admin, { AdminState } from "./admin";
import blogCategories from "./blogCategories";
import { CategoriesState } from "./blogCategories";
import institution, { InstitutionState } from "./institution";
import institutionUsers, { InstitutionUsersState } from "./institutionUsers";
import interestAreas, { InterestAreasState } from "./interestAreas";
import interestArea, { InterestAreaState } from "./interestArea";
import disciplines, { DisciplinesState } from "./disciplines";
import institutionCourses, {
  InstitutionCoursesState,
} from "./institutionCourses";
import courses, { CoursesState } from "./courses";
import course, { CourseState } from "./course";
import offers, { OffersState } from "./offers";
import offer, { OfferState } from "./offer";
import reservations, { ReservationsState } from "./reservations";
import reservation, { ReservationState } from "./reservation";
import leadReservations, { LeadReservationsState } from "./leadReservations";

interface StoreState {
  auth: AuthState;
  institutions: InstitutionsState;
  institutionUsers: InstitutionUsersState;
  institution: InstitutionState;
  blogPosts: BlogState;
  blogPost: BlogPostState;
  blogCategories: CategoriesState;
  admins: AdminsState;
  admin: AdminState;
  interestAreas: InterestAreasState;
  interestArea: InterestAreaState;
  disciplines: DisciplinesState;
  institutionCourses: InstitutionCoursesState;
  courses: CoursesState;
  course: CourseState;
  offers: OffersState;
  offer: OfferState;
  reservations: ReservationsState;
  reservation: ReservationState;
  leadReservations: LeadReservationsState;
}

export default createStore<StoreState>({
  modules: {
    auth,
    institutions,
    institutionUsers,
    institution,
    blogPosts,
    blogCategories,
    blogPost,
    admins,
    admin,
    interestAreas,
    interestArea,
    disciplines,
    institutionCourses,
    courses,
    course,
    offers,
    offer,
    reservations,
    reservation,
    leadReservations,
  },
});

declare module "@vue/runtime-core" {
  interface ComponentCustomProperties {
    $store: Store<StoreState>;
  }
}
