
import { Offer } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  computed: {
    offers() {
      return this.$store.state.offers.data?.result || [];
    },
  },
  methods: {
    getCourse(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline
        ?.educationInstitutionCourse?.course.name;
    },
    getDiscipline(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline?.name;
    },
    getInstitution(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline
        ?.educationInstitutionCourse?.educationInstitution?.abbreviation;
    },
  },
});
