<template>
  <div class="d-flex justify-content-between mb-4">
    <h1>Ofertas</h1>
    <pagination :current="current" :last="last" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    current(): number {
      return this.$store.state.offers.data?.currentPage || 1;
    },
    last(): number {
      return this.$store.state.offers.data?.totalPages || 1;
    },
  },
  methods: {
    navigate(offset: number) {
      this.$store.dispatch("offers/load", { offset });
    },
  },
});
</script>
