
import { InterestArea } from "@/types";
import { defineComponent, PropType } from "vue";
import FormField from "../FormField.vue";

export default defineComponent({
  components: { FormField },
  props: {
    submitText: { type: String, required: true },
    initialValue: {
      type: Object as PropType<Partial<InterestArea>>,
      required: false,
    },
  },
  created() {
    console.log("interest area form initial value is", this.initialValue);
    this.name = this.initialValue?.name ?? "";
    this.description = this.initialValue?.description ?? "";
    this.showOnHomePage = this.initialValue?.showOnHomePage ?? false;
  },
  data: () => ({
    name: "",
    description: "",
    showOnHomePage: false,
  }),
  methods: {
    submit() {
      this.$emit("send", {
        name: this.name,
        description: this.description,
        showOnHomePage: this.showOnHomePage,
        featuredImage: "no_image_for_now",
      });
    },
  },
});
