import {
  ResourceState,
  createResourceModule,
} from "./generic/createResourceModule";
import { Offer } from "@/types";
import api from "@/services/api";

export type OfferState = ResourceState<Offer>;

export default createResourceModule<OfferState>(async (offerId) => {
  const res = await api.get(`/offers/${offerId}/`);
  return res.data.result;
});
