import { PageResult } from "@/types";
import {
  ResourceState,
  createResourceModule,
} from "./generic/createResourceModule";
import api from "@/services/api";

export type ReservationsState = ResourceState<PageResult<unknown>>;

export default createResourceModule<ReservationsState>(async (args) => {
  console.log("getting reservations with", args);
  const { offset } = args as { offset: number };
  const res = await api.get("/student/reservations/", {
    params: { offset, limit: 10 },
  });
  console.log("res is", res);
  return res.data;
});
