<template>
  <h1>Detalhes do Curso</h1>
  <p><strong>Nome:</strong> {{ course?.name }}</p>
  <p>
    <strong>Descrição:</strong> {{ course?.description ?? "(sem descrição)" }}
  </p>
  <p><strong>Tipo:</strong> {{ course?.type }}</p>
  <p><strong>RCN:</strong> {{ course?.rcn }}</p>
  <p><strong>Criado em:</strong> {{ $datetime(course?.createdAt) }}</p>
  <p>
    <strong>Última atualização em:</strong> {{ $datetime(course?.updatedAt) }}
  </p>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  created() {
    this.$store.dispatch("course/load", this.$route.params.id);
  },
  computed: {
    course() {
      return this.$store.state.course.data;
    },
  },
});
</script>
