<template>
  <back
    :to="{ name: 'interest-area-details', params: { id: $route.params.id } }"
  />
  <h1>Editando área de interesse</h1>
  <interest-area-form
    submitText="Editar área de interesse"
    :initialValue="interestArea"
    v-if="interestArea"
    @send="update"
  />
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import InterestAreaForm from "@/components/interestAreas/InterestAreaForm.vue";
import { defineComponent } from "vue";
import api from "@/services/api";

export default defineComponent({
  components: { InterestAreaForm, Back },
  created() {
    this.$store.dispatch("interestArea/load", this.$route.params.id);
  },
  methods: {
    async update(data: unknown) {
      console.log("update interest area", data);
      try {
        const res = await api.patch(
          `/interest-area/${this.interestArea?.id}`,
          data
        );
        console.log("update interest area res", res);
        this.$router.push({
          name: "interest-area-details",
          params: { id: this.interestArea?.id ?? "" },
        });
      } catch (e) {
        console.log("error updating interest area", e);
      }
    },
  },
  computed: {
    interestArea() {
      return this.$store.state.interestArea.data;
    },
  },
});
</script>
