
import Back from "@/components/Back.vue";
import { InterestArea } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  components: { Back },
  created() {
    this.$store.dispatch("interestArea/load", this.$route.params.id);
  },
  computed: {
    interestArea(): InterestArea | undefined {
      return this.$store.state.interestArea.data;
    },
  },
});
