<template>
  <div class="d-flex justify-content-between mb-4">
    <h1>Reservas</h1>
    <pagination :current="current" :last="last" @navigate="navigate" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Pagination from "../Pagination.vue";

export default defineComponent({
  components: { Pagination },
  computed: {
    current() {
      return this.$store.state.reservations.data?.currentPage || 1;
    },
    last() {
      return this.$store.state.reservations.data?.totalPages || 1;
    },
  },
  methods: {
    navigate(offset: number) {
      this.$store.dispatch("reservations/load", { offset });
    },
  },
});
</script>
