import { Offer, PageResult } from "@/types";
import {
  createResourceModule,
  ResourceState,
} from "./generic/createResourceModule";
import api from "@/services/api";

export type OffersState = ResourceState<PageResult<Offer>>;

export default createResourceModule<OffersState>(
  async (args: unknown): Promise<OffersState> => {
    const { offset } = args as { offset: number };
    const res = await api.get("/offers/", { params: { offset } });
    return res.data;
  }
);
