<template>
  <back :to="{ name: 'interest-areas' }" />
  <div class="d-flex justify-content-between">
    <h1>Detalhes da Área de Interesse</h1>
    <div>
      <router-link
        :to="{ name: 'interest-area-edit', params: { id: interestArea?.id } }"
        class="btn btn-primary"
      >
        Editar
      </router-link>
    </div>
  </div>

  <p><strong>Nome:</strong> {{ interestArea?.name }}</p>
  <p>
    <strong>Descrição:</strong>
    {{ interestArea?.description ?? "(sem descrição)" }}
  </p>
  <p>
    <strong>Mostrar na tela inicial?</strong>
    {{ interestArea?.showOnHomePage ? "Sim" : "Não" }}
  </p>
  <p>
    <strong>Cadastrado em:</strong>
    {{ new Date(interestArea?.createdAt).toLocaleDateString() }} às
    {{ new Date(interestArea?.createdAt).toLocaleTimeString() }}
  </p>
  <p>
    <strong>Última atualização em:</strong>
    {{ new Date(interestArea?.updatedAt).toLocaleDateString() }} às
    {{ new Date(interestArea?.updatedAt).toLocaleTimeString() }}
  </p>
</template>

<script lang="ts">
import Back from "@/components/Back.vue";
import { InterestArea } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  components: { Back },
  created() {
    this.$store.dispatch("interestArea/load", this.$route.params.id);
  },
  computed: {
    interestArea(): InterestArea | undefined {
      return this.$store.state.interestArea.data;
    },
  },
});
</script>
