import { Module } from "vuex";

export interface ResourceState<T> {
  loading: boolean;
  error: boolean;
  data?: T;
}

export const createResourceModule = <ResourceType>(
  load: (args: unknown) => Promise<ResourceType>
): Module<ResourceState<ResourceType>, unknown> => {
  return {
    namespaced: true,
    state: { loading: false, error: false },
    mutations: {
      setLoading(state) {
        state.loading = true;
        state.error = false;
      },
      setError(state) {
        state.error = true;
        state.loading = false;
      },
      setLoaded(state, payload) {
        state.loading = false;
        state.error = false;
        state.data = payload;
      },
    },
    actions: {
      async load({ commit }, payload) {
        commit("setLoading");
        try {
          commit("setLoaded", await load(payload));
        } catch (e) {
          commit("setError");
        }
      },
    },
  };
};
