<template>
  <table class="table">
    <thead>
      <th>Instituição</th>
      <th>Curso</th>
      <th>Disciplina</th>
      <th></th>
    </thead>
    <tbody>
      <tr v-for="offer in offers" :key="offer.id">
        <td>{{ getInstitution(offer) }}</td>
        <td>{{ getCourse(offer) }}</td>
        <td>{{ getDiscipline(offer) }}</td>
        <td>
          <router-link
            :to="{ name: 'offer-details', params: { id: offer.id } }"
            class="btn btn-primary btn-sm"
          >
            Ver mais
          </router-link>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script lang="ts">
import { Offer } from "@/types";
import { defineComponent } from "vue";

export default defineComponent({
  computed: {
    offers() {
      return this.$store.state.offers.data?.result || [];
    },
  },
  methods: {
    getCourse(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline
        ?.educationInstitutionCourse?.course.name;
    },
    getDiscipline(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline?.name;
    },
    getInstitution(offer: Offer) {
      return offer?.educationInstitutionCourseDiscipline
        ?.educationInstitutionCourse?.educationInstitution?.abbreviation;
    },
  },
});
</script>
