import {
  ResourceState,
  createResourceModule,
} from "./generic/createResourceModule";
import { LeadReservation, PageResult } from "@/types";
import api from "@/services/api";

export type LeadReservationsState = ResourceState<PageResult<LeadReservation>>;

export default createResourceModule(async (args) => {
  const { offset } = args as { offset: number };
  const res = await api.get("/lead/reservations", {
    params: { offset, limit: 10 },
  });
  console.log("res is", res);
  return res.data.result;
});
