import { Course } from "@/types";
import {
  createResourceModule,
  ResourceState,
} from "./generic/createResourceModule";
import api from "@/services/api";

export type CourseState = ResourceState<Course>;

export default createResourceModule<Course>(
  async (courseId): Promise<Course> => {
    const res = await api.get(`/courses/${courseId}/`);
    return res.data.result;
  }
);
